.subSubjectCard{max-width:calc(33.3333333333% - 100px);width:100%;padding:40px 20px;border:2px solid rgba(0,0,0,.25);display:flex;flex-direction:column;background-color:#5caa9d;height:370px}.subSubjectCard .subjectName{font-size:52px;font-weight:700;text-align:center;font-family:Roboto;color:#fff;text-shadow:2px 3px 1px #01615f;text-transform:uppercase;position:relative}.subSubjectCard .subSubjectContainer{flex:1;display:flex;flex-direction:column;gap:30px;margin-left:10px;margin-top:40px;max-height:260px;height:100%;overflow:auto}.subSubjectCard .subSubjectContainer::-webkit-scrollbar{width:8px}.subSubjectCard .subSubjectContainer::-webkit-scrollbar-track{background:hsla(0,0%,100%,.2);border-radius:4px}.subSubjectCard .subSubjectContainer::-webkit-scrollbar-thumb{background-color:#fff;border-radius:4px;border:2px solid rgba(0,0,0,0);background-clip:content-box}.subSubjectCard .subSubjectContainer .manage{display:flex;gap:50px;padding-right:20px}.subSubjectCard .subSubjectContainer .manage .subSubjectName{font-weight:600;flex:1;color:#fff;font-family:Roboto;text-transform:uppercase;font-size:30px;text-shadow:2px 2px 1px rgba(0,0,0,.5)}